<template>
  <el-dialog title="" v-model="show" top="10%" size="large" >
    <el-form :model="form" :rules="rules" ref="ruleForm" label-width="120px">
      <el-form-item label="" prop="name">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-radio-group v-model="form.sex">
          <el-radio label=""></el-radio>
          <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="citizenship">
        <el-select v-model="form.citizenship" placeholder="" >
          <el-option label="" value="Japan"></el-option>
          <el-option label="" value="China"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="birthday">
        <el-date-picker
          v-model="form.birthday"
          type="date"
          placeholder=""
          :picker-options="pickerOptions0">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="telephone">
        <el-input v-model="form.telephone"  type="number"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="form.email"></el-input>
      </el-form-item>
      <el-form-item label="" prop="company">
        <el-select v-model="form.company" placeholder="" >
          <el-option
            v-for="item in companyoptions"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="entrytime">
        <el-date-picker
          v-model="form.entrytime"
          placeholder=""
          :picker-options="pickerOptions0">
        </el-date-picker>
      </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.department"></el-input>
        </el-form-item>
        <el-form-item>
          <el-upload
            action="//jsonplaceholder.typicode.com/posts/"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :file-list="fileList">
           <el-button size="small" type="primary"></el-button>
           <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
         </el-upload>
        </el-form-item>
      </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="display"> </el-button>
      <el-button type="primary" @click="createstaff"> </el-button>
    </div>
  </el-dialog>
</template>
<script type="text/javascript">
  export default {
    data() {
      return {
        // 
        form: {
          name: '',
          sex: '',
          citizenship: '',
          birthday: '',
          telephone: '',
          email: '',
          company: '',
          entrytime: '',
          fileList: [{
            name: '',
            url: ''
          }]
        },
        // 
        // trigger: blur  change 
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 2, max: 5, message: ' 3  5 ', trigger: 'blur' }
          ],
          sex: [
            { required: true, message: '', trigger: 'change' }
          ],
          citizenship: [
            { required: true, message: '', trigger: 'change' }
          ],
          birthday: [
            { type: 'date', required: true, message: '', trigger: 'change' }
          ],
          telephone: [
            { required: true, message: '', trigger: 'blur' },
            { min: 9, max: 11, message: '', trigger: 'blur' }
          ],
          email: [
            { required: true, message: '', trigger: 'blur' },
            { type: 'email', message: '', trigger: 'blur' }
          ],
          company: [
            { required: true, message: '', trigger: 'blur' }
          ],
          entrytime: [
            { type: 'date', required: true, message: '', trigger: 'change' }
          ]
        },
        // 
        pickerOptions0: {
          disabledDate(time) {
            return time.getTime() > Date.now() - 8.64e7
          }
        },
        // 
        // 
        companyoptions: [{
          value: 'haoyunlai',
          label: ''
        }, {
          value: 'haoxianglai',
          label: ''
        }, {
          value: 'laibulai',
          label: ''
        }, {
          value: 'bulia',
          label: ''
        }, {
          value: 'nizou',
          label: ''
        }]
      }
    },
    props: {
      show: {
        type: Boolean,
        default() {
          return false
        }
      }
    },
    methods: {
      display: function () {
        this.show = false
        this.$emit('display')
      },
      handleRemove(file, fileList) {
        console.log(file, fileList)
      },
      handlePreview(file) {
        console.log(file)
      },
      createstaff() {
        console.log('lallal')
        let url = '/api/staff'
        this.$http.post(url, this.form)
        .then((response) => {
          this.$set('form', {})
        }, (response) => {
          console.log('error')
        })
        this.show = false
      }
    }
  }
</script>
<style type="text/css">
  
</style>



