<template>
  <div class="company">        
    <div class="container">
      <breadcrumb>
        <breadcrumbitem>
          <router-link to="/" exact><i class="iconfont icon-homepage"></i> </router-link>
        </breadcrumbitem>
        <breadcrumbitem>
          <router-link to="../company" exact></router-link>
        </breadcrumbitem>
        <breadcrumbitem label=""></breadcrumbitem>
      </breadcrumb>
      <div class="from">
        <el-col :span="15">
          <el-form :model="ruleForm" :ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="name">
              <el-input v-model="ruleForm.name" :readonly="true"></el-input>
            </el-form-item>
            <el-form-item label="" prop="buyerid">
              <el-input v-model="ruleForm.buyerid" :readonly="true"></el-input>
            </el-form-item>
            <el-form-item label="" prop="sellerid">
              <el-input v-model="ruleForm.sellererid" :readonly="true"></el-input>
            </el-form-item>
            <el-form-item label="" prop="tel">
              <el-input v-model="ruleForm.tel" :readonly="true"></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="ruleForm.email" :readonly="true"></el-input>
            </el-form-item>
            <el-form-item label="" prop="address">
              <el-input type="textarea" v-model="ruleForm.address" :readonly="true"></el-input>
            </el-form-item>
            </br></br>
            <el-form-item>
              <el-button type="success" v-on:click="updatepage"><i class ="iconfont icon - icon-adit"></i> </el-button>
              <el-button type="danger" @click="deleteForm('ruleForm')"><i class ="iconfont icon - icon-delete"></i> </el-button>
              <el-button type="info" v-on:click="companypage"><i class ="iconfont icon - icon-return"></i> </el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </div>
      
    </div>
  </div>
</template>

<script>
  import { breadcrumb, breadcrumbitem } from 'components/breadcrumb'
  
  export default {
    components: {
      breadcrumb,
      breadcrumbitem
    },
    data() {
      return {
        ruleForm: {
          name: '',
          buyerid: '',
          sellerid: '',
          tel: '',
          email: '',
          address: ''
        }
      }
    },
    methods: {
      companypage() {
        window.location('#/company')
      },
      updatepage() {
        window.location('#/company/update')
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
      deleteForm(formName) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          })
          window.location('#/company')
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    }
  }
</script>

<style scoped>

</style>



